a{
    color: inherit;
}

.social-icon{
    display: inline-block;
    margin: 0 20px;
    transition: 1s;
}
.social-icon:hover{
    transform: scale(1.2);
    cursor: pointer;
    fill: #767676;
}

@media only screen and (min-width: 1450px){
    .social-cions-container{
          position: absolute;
          bottom: 15px;
          left: 50%;
          transform: translateX(-50%);
    }
}