    .header-container{
         color: #767676;
    }
    h1{
         display: inline-block;
         margin: 0;
         font-size: 1.5rem;
         line-height: 32px;
         width: 190px;
         text-align: left;
    }
    .logo{
         margin: 0;
         width: 30px;
         height: 30px;
         position: absolute;
         right: 5px;
         bottom: 3px;
    }

    .logo-wrapper{
         position: relative;
         display: inline-block;
    }
      

    @media only screen and (min-width: 500px){

     h1{
          width: 390px;
     }
           
    }

    @media only screen and (min-width: 900px){

     h1{
          width: 520px;
          font-size: 2rem;
          margin-bottom: 6px;
     }

     .logo{
          width: 40px;
          height: 40px;
     }
           
    }