.table-section{
    margin-top: 20px;
}
.tableContainer{
    max-width: 820px;
    height: 810px;
    box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.25);
    border-radius: 5px;
}
.table{
    min-width: 200px;
    max-width: 820px;

}
.table-title{
    margin: 25px 0 30px 0;
    font-size: 1.5rem;
    font-weight: 700;
    display: inline-block;

}
.tableHead{
    background-color: #fff;
}
.table-head-content{
    color: #AAAAAA !important;
    font-family: 'Open Sans', sans-serif !important;
    font-weight: 600 !important;
}

.table-body-row:hover{
cursor: pointer;
transform: scale(1.02);
}
.table-body-row-active {
    cursor: pointer;
    background-color: rgb(255, 223, 226) !important;
    border: 3px solid rgb(255, 185, 192) !important;
}

.table-body-row:nth-child(2n - 1){
    background-color: #F3F3F3;
    border: none;
}
.table-body-row:nth-child(2n){
    background-color: rgb(248, 248, 248);
    border: none;
}
.table-body-content{
    font-family: 'Open Sans', sans-serif !important;
}

