.state-card-wrapper{
    width: 100%;
    height: 765px;
    position: relative;
    margin-top: 50px;
    margin-bottom: 50px;
}
.state-card-container{
    width: 100%;
    max-width: 230px;
    background-color: #fff;
    text-align: center;
    box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.25);
    color: #5B5B5B;
    padding: 20px 20px;
    border-radius: 5px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
}
/* Card title */
.state-name{
font-size: 1.3rem;
font-weight: 700;
margin: 0 0 10px 0;
}

.update-info{
    font-size: .6rem;
    font-weight: 700;
    margin: 0;    
}


/* stat cards */
.state-stats-wrapper{
    width: 100%;
    height: 340px;
    margin-top: 20px;
    position: relative;
}

.state-stats-container{
    display: grid;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    grid-template-columns: repeat(autofill, minmax(168px,168px));
    grid-gap: 20px;
}

.state-stat{
    width: 168px;
    height: 60px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
    padding: 7px 10px;
    text-align: left;
    border-radius: 5px;
    color: #fff;
}
.state-stat p{
    margin: 0;
}
.state-stat-info{
    font-size: .8rem;
}
.state-stat-value .value{
    display: inline-block;
    font-weight: 700;
    font-size: 1.45rem;
}
.state-stat-value .add{
    float: right;
    margin-top: 8px;
}
.state-stat-confirmed{
    background-color: #4FC3F7;
}
.state-stat-active{
    background-color: #EF5350;
}
.state-stat-recovered{
    background-color: #8BC34A;
}
.state-stat-deaths{
    background-color: #C4C4C4;
}

/* PIE CHART  */
.pieChart{
width: 100%;
height: 80%;
}

@media only screen and (min-width: 600px){
    .state-card-wrapper{
        height: 635px;
    }
    .state-card-container{
        width: 80%;
        max-width: 400px;
    }
    .state-name{
        font-size: 1.5rem;
    }
    .update-info{
        font-size: .7rem;
    }
    .state-stats-wrapper{
        margin-top: 30px;
        height: 200px;
    }
    .state-stats-container{
        display: grid;
        grid-template-columns: repeat(2, 168px);
        grid-gap: 20px;
    }
    .pieChart{
        height: 300px;
    }
}

@media only screen and (min-width: 1450px){
    .state-card-container{
        width: 80%;
        max-width: 400px;
    }
    .state-card-container{
        position: static;
        left: none;
        transform: none;
    }
    .pieChart{
        height: 200px;
    }
}