.svg-container{
  margin: 80px 0 0 0;
  width:  100%;
  max-width: 820px;
}

 .svg-container svg path:hover{
    transition: .3s;
    fill: #fff !important;
 }

 .path-active{
  fill: #fff !important;
 }

 @media only screen and (min-width: 1450px){

    .svg-container{
        max-width: 530px;
        margin-top: 50px;
      }

 }