.cards-section{
    min-height: 750px;
}
.card-container-title{
    margin: 40px 0 30px;
    font-size: 1.5rem;
    font-weight: 700;
}

.card-container{
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(190px,190px));
    grid-gap: 20px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
}

.card{
    background-color: #fff;
    text-align: center;
    width: 190px;
    height: 151px;
    border-radius: 5px;
    box-shadow: 0px 4px 15px #B5B5B5;
    transition: 0.3s;
}
.card:hover{
    transform: scale(1.1);
}
.card-title{
    margin: 16px 0 14px 0;
}

.card-value{
    font-size: 2rem;
    font-weight: 700;
    margin: 0;
}

.card-addition{
    margin: 14px 0 16px 0;
}

.card-confirmed{
    color:#4FC3F7;
}

.card-active{
    color: #EF5350;
}

.card-recovered{
    color: #8BC34A;
}

.card-deaths{
    color: #AAAAAA;
}

@media only screen and  (min-width: 500px){
    .card-container{
        grid-template-columns: repeat(2, 190px);
        grid-gap: 40px;
    }
    .cards-section{
        min-height: 430px;
    }
    
}
@media only screen and (min-width: 900px){
    .cards-section{
        min-height: 0;
    }
    .card-container{
        grid-template-columns: repeat(auto-fill, minmax(190px,190px));
        grid-gap: 20px;
        position: static;
        transform: none;
    }
}